body{background-attachment:fixed;grid-template-columns:1fr 1fr 1fr;height:100%;width:100%;background-image:url("/src/img/background.jpg");font:20px sans-serif;padding-top:35px}main{display:grid;margin-top:100px;grid-template-columns:1fr 1fr 1fr 1fr}header h1{color:#2c3a47;text-align:center;font-size:50px;font-weight:bold;margin-bottom:35px}.cards{padding:20px;margin:0 auto}.card-name{vertical-align:middle;font-weight:bold;width:230px;text-align:center;height:auto;background-color:#00b894;color:#fff;padding:10px;border-radius:10px 10px 0 0px}.card-img{background-color:rgba(255,255,255,.8);height:250px}.card-short-desc{color:#0a3d62;text-align:center;font-weight:bold;width:230px;background-color:rgba(255,255,255,.8);padding:10px;height:auto;border-radius:0 0 10px 10px}.card-desc{color:#0a3d62;text-align:center;font-weight:bold;width:230px;background-color:rgba(255,255,255,.8);padding:10px;height:auto;border-radius:0 0 10px 10px;visibility:hidden}.btn-add{margin-top:15px;text-align:center;width:210px;background-color:#1dd1a1;color:#fff;padding:20px;border-radius:5px}.btn-submit{margin-top:15px;text-align:center;width:210px;background-color:#1dd1a1;color:#fff;padding:20px;border-radius:5px}.btn-edit{margin-top:15px;text-align:center;width:210px;background-color:#fdcb6e;color:#fff;padding:20px;border-radius:5px}.btn-delete{margin-top:15px;text-align:center;width:210px;background-color:#ff6b6b;color:#fff;padding:20px;border-radius:5px}